<template>
  <div>
    <top></top>
    <el-carousel height="450px" style="width:100%" :interval="5000" arrow="always">
      <el-carousel-item v-for="item in banner" :key="item">
        <img :src="item.url" style="height:450px;width:100%">
      </el-carousel-item>
    </el-carousel>
    <div class="box mar">
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="indexFour">
            <img src="../../static/indexFour1.png">
          </div>
        </el-col>
        <el-col :span="6">
          <div class="indexFour">
            <img src="../../static/indexFour2.png">
          </div>
        </el-col>
        <el-col :span="6">
          <div class="indexFour">
            <img src="../../static/indexFour3.png">
          </div>
        </el-col>
        <el-col :span="6">
          <div class="indexFour">
            <img src="../../static/indexFour4.png">
          </div>
        </el-col>
      </el-row>
      <div class="indexList mar fffbg">
        <div class="indexListTitle">
          <div class="sx"></div>
          <span class="fontTitle">品牌精选</span>
          <span class="fontDesc">品质好，实用度高</span>
        </div>
        <div class="indexlist">
          <li v-for="(item, index) in list" :key="index" @click="$router.push({path:'/member'})">
            <img :src="item.images.split(',')[0]">
            <div class="indexlistTitle">
              <span class="price" style="float: right">
                <span class="money">{{item.price / 100}}元</span>
              </span>
              <div class="name">{{item.name}}</div>
            </div>
            <!-- <el-button @click="$router.push({path:'/member'})" type="primary">立即购买</el-button> -->
          </li>
        </div>
      </div>
      <div class="indexList mar fffbg">
        <div class="indexListTitle">
          <div class="sx"></div>
          <span class="fontTitle">新品筹备</span>
          <span class="fontDesc">特例独行，小资必备</span>
        </div>
        <div class="indexlist">
          <li v-for="(item, index) in list2" :key="index" @click="$router.push({path:'/member'})">
            <img :src="item.images.split(',')[0]">
            <div class="indexlistTitle">
              <span class="price" style="float: right">
                <span class="money">{{item.price / 100}}元</span>
              </span>
              <div class="name">{{item.name}}</div>
            </div>
            <!-- <div class="indexlistTitle">{{item.name}}<span style="float: right">单价：{{item.price / 100}}元</span></div> -->
            <!-- <el-button @click="$router.push({path:'/member'})" type="primary">立即购买</el-button> -->
          </li>
        </div>
      </div>
    </div>
    <foot></foot>
  </div>
</template>

<script>
import foot from "./public/foot.vue";
import top from "./public/top.vue";

export default {
  components: {
    foot,
    top
  },
  data() {
    return {
      banner: [
        {
          url: require("../../static/banner1.jpg")
        },
        {
          url: require("../../static/banner2.jpg")
        }
      ],
      list: [],
      list2: []
    };
  },
  mounted() {
    this.getList();
    this.getList2();
    console.log(process.env.API_HOST);
    console.log(this.url);
    const { inviteCode } = this.$route.query;
    sessionStorage.setItem("inviteCodeParent", inviteCode);
  },
  methods: {
    getList() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"goods?name=&state=0&page=0&size=5&goodBrand=true",
          "/giftmall/goods?name=&state=0&page=0&size=5&goodBrand=true",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.list = response.data.data.list;
          }
        });
    },
    getList2() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"goods?name=&state=0&page=0&size=5&newGoods=true",
          "/giftmall/goods?name=&state=0&page=0&size=5&newGoods=true",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.list2 = response.data.data.list;
          }
        });
    }
  }
};
</script>

<style scoped>
.fffbg {
  background: #fff;
}
.indexFour {
  text-align: center;
}
.indexFour img {
  width: 100%;
}
.mar {
  margin-top: 20px;
}
.indexListTitle {
  border-bottom: 1px solid #eaeaea;
  height: 60px;
  line-height: 60px;
  vertical-align: middle;
}
.sx {
  background: #ec3440;
  height: 30px;
  width: 5px;
  border-radius: 3px;
  float: left;
  margin-left: 15px;
  margin-right: 10px;
  margin-top: 15px;
}
.fontTitle {
  font-size: 24px;
  float: left;
  margin-right: 10px;
}
.fontDesc {
  font-size: 16px;
  color: #ccc;
}
.indexlist {
  overflow: hidden;
}
.indexlist li {
  width: 20%;
  padding: 20px;
  float: left;
  box-sizing: border-box;
  border-right: 1px solid #eaeaea;
}
.indexlist img {
  width: 100%;
}
.indexlistTitle {
  line-height: 25px;
  font-size: 16px;
  margin-top: 5px;
  margin-bottom: 10px;
}
.footer {
  background: #191919;
  margin-top: 30px;
}
</style>

<style scoped lang="less">
.indexlist {
  li {
    border: 3px solid transparent;
    border-right: 1px solid #eaeaea;
    padding-right: 22px;
    cursor: pointer;
    &:hover {
      padding-right: 20px;
      border: 3px solid #ff4d70;
      border-radius: 5px;
    }
    img {
      width: 194px;
      height: 194px;
    }
    .indexlistTitle {
      display: flex;
      flex-direction: column;
      margin: 0;
      .price {
        padding: 10px 0;
        .money {
          color: #ff4d70;
          font-size: 24px;
        }
      }
      .name {
        padding-top: 10px;
        border-top: 1px solid #eaeaea;
        font-size: 14px;
      }
    }
  }
}
</style>


